<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <el-tabs v-model="searchList.tabType" @tab-click="getData">
        <el-tab-pane label="全部" name="0"></el-tab-pane>
        <el-tab-pane label="待审核" name="10"></el-tab-pane>
        <el-tab-pane label="退款中" name="40"></el-tab-pane>
        <el-tab-pane label="已退款" name="50"></el-tab-pane>
        <el-tab-pane label="已驳回" name="20"></el-tab-pane>
        <el-tab-pane label="已撤销" name="60"></el-tab-pane>
        <el-tab-pane label="退款失败" name="70"></el-tab-pane>
      </el-tabs>
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="订单编号：">
            <el-input style="width: 203px" v-model="searchList.orderNo" clearable placeholder="订单编号"></el-input>
          </el-form-item>
          <el-form-item label="退单编号：">
            <el-input style="width: 203px" v-model="searchList.afterSaleNo" clearable placeholder="退单编号"></el-input>
          </el-form-item>
          <el-form-item label="申请时间：">
            <el-date-picker v-model="searchList.time"
              type="daterange"
              value-format="yyyy-MM-dd"
              :picker-options="pickerOptions"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="商品名称：">
            <el-input style="width: 203px" v-model="searchList.name" clearable placeholder="商品名称"></el-input>
          </el-form-item>
          <el-form-item label="发货状态：">
            <el-select v-model="searchList.status" placeholder="请选择发货状态">
              <el-option label="已发货" value="2"></el-option>
              <el-option label="未发货" value="1"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container" style="margin-top:10px" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card style="margin-top:20px;" class="filter-container" shadow="never">
      <el-table :data="list" border tooltip-effect="dark" style="width: 100%">
        <el-table-column prop="afterSaleNo" label="退款申请单号" width="260"></el-table-column>
        <el-table-column label="订单编号" width="260">
          <template slot-scope="scope">
            <div class="brandFont curP" @click="$router.push({path:'/order/orderList/orderinfo',query:{orderId:scope.row.orderId}})">{{scope.row.orderNo}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label="申请时间" width="300"></el-table-column>
        <el-table-column prop="applyAmount" label="申请退款金额" width="180"></el-table-column>
        <el-table-column prop="baseName" label="退款原因" width="300"></el-table-column>
        <el-table-column label="退款状态" width="180">
          <template slot-scope="scope">
            <div style="color:#E6A23C;" v-if="scope.row.auditStatus == 10">待审核</div>
            <div style="color:#E6A23C;" v-if="scope.row.auditStatus == 23">待复审</div>
            <div style="color:#E6A23C;" v-if="scope.row.auditStatus == 40">退款中</div>
            <div style="color:#67C23A;" v-if="scope.row.auditStatus == 50">已退款</div>
            <div style="color:#F56C6C;" v-if="scope.row.auditStatus == 20">已驳回</div>
            <div style="color:#F56C6C;" v-if="scope.row.auditStatus == 60">已撤销</div>
            <div style="color:#F56C6C;" v-if="scope.row.auditStatus == 70">退款失败</div>
          </template>
        </el-table-column>
        <el-table-column label="订单取消时状态" width="180">
          <template slot-scope="scope">
            <div v-if="scope.row.orderStatus == 0">待付款</div>
            <div v-if="scope.row.orderStatus == 1">待发货</div>
            <div v-if="scope.row.orderStatus == 2">已发货</div>
            <div v-if="scope.row.orderStatus == 3">已完成</div>
            <div v-if="scope.row.orderStatus == 4">已关闭</div>
          </template>
        </el-table-column>
        <el-table-column label="发货状态" width="180">
          <template slot-scope="scope">
            <div v-if="scope.row.status == 0">待付款</div>
            <div v-if="scope.row.status == 1">待发货</div>
            <div v-if="scope.row.status == 2">已发货</div>
            <div v-if="scope.row.status == 3">已完成</div>
            <div v-if="scope.row.status == 4">已关闭</div>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="180" fixed="right">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="$router.push({path:'/afterSaleManage/refund/refundInfo',query:{id:scope.row.id}})">查看</el-button>
            <el-button style="color:#E6A23C;" v-if="scope.row.auditStatus == 10" :loading="passLoading" @click="passItem(scope.row)" type="text" size="small">审核通过</el-button>
            <el-button style="color:#F56C6C;" v-if="scope.row.auditStatus == 10" @click="rejectItem(scope.row)" type="text" size="small">驳回</el-button>
            <el-button style="color:#E6A23C;" v-if="scope.row.auditStatus == 70" @click="againItem(scope.row)" type="text" size="small">再次退款</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-end" style="padding-top:20px;">
        <div>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page-sizes="[5,10,15,20]"
            :page-size="searchList.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="listTotal">
          </el-pagination>
        </div>
      </div>
    </el-card>
    <!-- 驳回弹出框 -->
    <rejectShow :rejectShow="rejectShow" :rejectForm="rejectForm" @rejectShowClose="rejectShowClose" @rejectFun="rejectFun"></rejectShow>
    <!-- 确认信息 -->
    <confirmShow :confirmShow="confirmShow" :confirmLoading="confirmLoading" :confirmForm="confirmForm" @confirmShowClose="confirmShowClose" @confirmFun="confirmFun"></confirmShow>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { findByCondition , reject , confirm , approve ,continueRefund} from '@/api/afterSaleManage/refund'
import rejectShow from '../components/rejectShow';
import confirmShow from '../components/confirmShow';
export default {
  data(){
    return{
      searchList:{
        tabType:0,
        pageNum:1,
        pageSize:5,
        time:[]
      },
      passLoading:false,
      list:[],
      listTotal:0,
      pickerOptions: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime()- 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
      rejectShow:false,
      rejectForm:{},
      confirmShow:false,              // 确认信息弹出
      confirmLoading:false,           // 确认信息loading
      confirmForm:{},                 // 确认信息数据
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    if(this.$route.query.tabType) this.searchList.tabType = this.$route.query.tabType+''
    this.getData();
  },
  methods:{
    // 列表 - 获取数据
    getData(){
      this.searchList.startCreateTime = this.searchList.time[0] || ''
      this.searchList.endCreateTime	 = this.searchList.time[1] || ''
      this.searchList.auditStatus = this.searchList.tabType == 0 ? '' : this.searchList.tabType
      console.log('请求的数据',this.searchList)
      findByCondition(this.searchList).then(res=>{
        console.log('退款列表',res)
        if(res.code == 200){
          this.list = res.data.list;
          this.listTotal = res.data.total
        }
      })
    },
    // 搜索栏 - 重置
    reset(){
      this.searchList.name = ''
      this.searchList.startCreateTime = ''
      this.searchList.endCreateTime = ''
      this.searchList.time = []
      this.searchList.orderNo = ''
      this.searchList.afterSaleNo = ''
      this.searchList.status = ''
      this.getData()
    },
    // 列表 - 重新退款
    againItem(e){
      this.$confirm(`退款金额：￥${e.refundAmount}`, '重新退款确认', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        continueRefund({id:e.id}).then(res=>{
          console.log('重新退款',res)
          if(res.code == 200){
            this.$message({
              message: res.message,
              type: 'success'
            });
            this.getData()
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    // 列表 - 驳回
    rejectItem(e){
      this.rejectForm.id = e.id
      this.rejectShow = true
    },
    // 驳回 - 关闭
    rejectShowClose(e){
      console.log('父级关闭',e)
      this.rejectShow = false
    },
    // 驳回 - 请求
    rejectFun(e){
      console.log('父级确认',e)
      this.rejectLoading = true;
      reject(e).then(res => {
        console.log('驳回',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.rejectForm.reason = ''
          this.getData()
          this.rejectShow = false;
          setTimeout(()=>{
            this.rejectLoading = false;
          },1000)
        }else{
          this.rejectLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 列表 - 通过
    passItem(e){
      this.passLoading = true;
      confirm({id:e.id}).then(res=>{
        console.log('确认数据',res)
        if(res.code == 200){
          res.data.isFreight = '1' 
          if(res.data.flag == 1) res.data.isFreight = '0'  
          this.confirmForm = res.data
          this.confirmShow = true
          setTimeout(()=>{
            this.passLoading = false
          },1000)
        }else{
          this.passLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    confirmFun(e){
      console.log('this.confirmForm',e)
      this.confirmLoading = true
      approve(e).then(res=>{
        console.log('审核通过',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.getData()
          this.confirmShow = false;
          setTimeout(()=>{
            this.confirmLoading = false;
          },1000)
        }else{
          this.confirmLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    confirmShowClose(e){
      console.log('父级关闭')
      this.confirmShow = false
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    }
  },
  components:{
    rejectShow,
    confirmShow
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .checkBoxDiv{
    font-size: 14px;
    margin-left: 40px;
    line-height: 19px;
    color: #606266;
  }
  .active {
    height: 70px;
  }
</style>