<template>
  <el-dialog title="您要驳回该退单的退回申请吗？" :visible.sync="rejectShow" :close-on-click-modal="false" :close-on-press-escape="false" width="30%">
    <el-input v-model.trim="rejectForm.reason" type="textarea" placeholder="请输入驳回原因" maxlength="50" show-word-limit></el-input>
    <div class="flex-row-end mt20">
      <el-button @click="rejectShowClose">取 消</el-button>
      <el-button :loading="rejectLoading" type="primary" @click="rejectFun">确 定</el-button>
    </div>
  </el-dialog>
</template>
<script>
export default {
  props: {
    rejectShow: {
      type: Boolean,
      default: false
    },
    rejectForm:{
      type:Object,
    },
    rejectLoading:{
      type: Boolean,
      default: false
    }
  },
  methods:{
    rejectFun(){
      if(!this.rejectForm.reason){
        this.$message.error('请输入驳回原因');
        return;
      }
      this.$emit('rejectFun', this.rejectForm);
    },
    rejectShowClose(){
      this.$emit('rejectShowClose', true);
    }
  }
}
</script>
