import request from '@/utils/request'
// 仅退款列表
export function findByCondition(data) {
  return request({
    url: `/order/business/refund/findByCondition`,
    method: 'post',
    data: data
  })
}
// 仅退款驳回
export function reject(data) {
  return request({
    url: `/order/business/refund/reject`,
    method: 'post',
    data: data
  })
}
