<template>
  <el-dialog title="退款信息确认" :visible.sync="confirmShow" :close-on-click-modal="false" :close-on-press-escape="false" width="50%">
    <el-form :model="confirmForm" class="demo-form-inline" label-width="120px">
      <div class="flex-row-start fw">
        <el-form-item label="退单编号：" style="width:50%;">
          <div class="mainFont">{{confirmForm.afterSaleNo}}</div>
        </el-form-item>
        <el-form-item label="退款原因：" style="width:50%;">
          <div class="mainFont">{{confirmForm.baseName ? confirmForm.baseName : '-'}}</div>
        </el-form-item>
        <el-form-item label="退货类型：" style="width:50%;">
          <div class="mainFont" v-if="confirmForm.type == 0">仅退款</div>
          <div class="mainFont" v-if="confirmForm.type == 10">退货退款</div>
          <div class="mainFont" v-if="confirmForm.type == 20">换货</div>
          <div class="mainFont" v-if="confirmForm.type == 30">维修</div>
        </el-form-item>
        <el-form-item label="退款方式：" style="width:50%;">
          <div class="mainFont">微信原路返回</div>
        </el-form-item>
        <el-form-item label="商品实付金额：" style="width:50%;">
          <div class="mainFont">￥{{confirmForm.payPrice}}</div>
        </el-form-item>
        <el-form-item label="运费：" style="width:50%;">
          <div class="mainFont">￥{{confirmForm.freightPrice}}</div>
        </el-form-item>
        <el-form-item label="是否扣除运费：" style="width:100%;" required>
          <el-radio v-model="confirmForm.isFreight" :disabled="confirmForm.flag == 1" label="1">退运费</el-radio>
          <el-radio v-model="confirmForm.isFreight" :disabled="confirmForm.flag == 1" label="0">不退运费</el-radio>
        </el-form-item>
        <el-form-item v-if="confirmForm.flag == 1" label="" style="width:100%;">
          <div style="width:100%;" class="dangerFont">
            该订单运费已经被扣除
          </div>
        </el-form-item>
        <div style="width:100%;">
          <el-form-item label="应退金额：" style="width:50%;">
            <div class="mainFont">￥{{confirmForm.isFreight == 0 ? confirmForm.payPrice : confirmForm.totalPayPrice}}</div>
          </el-form-item>
        </div>
        <div style="width:100%;">
          <el-form-item label="退款金额：" required>
            ￥<el-input-number 
              v-model="confirmForm.amount"
              :placeholder="confirmForm.isFreight == 0 ? ''+confirmForm.payPrice : ''+confirmForm.totalPayPrice" 
              :min="0" 
              :controls="false" :precision="2"></el-input-number>
          </el-form-item>
        </div>
        <div style="width:100%;">
          <el-form-item style="width:100%;" label="退款说明：">
            <el-input
              style="width:400px"
              type="textarea"
              placeholder="请输入退款说明"
              v-model="confirmForm.refundDesc"
              maxlength="100"
              show-word-limit
            >
            </el-input>
          </el-form-item>
        </div>
        <div>
          <el-form-item label="退款备注：">
            <div style="color:#F56C6C;">
              如果部分退款，一定要与卖家提前沟通，经卖家同意，否则可能会造成投诉；
            </div>
          </el-form-item>
        </div>
      </div>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="confirmShowClose">取 消</el-button>
      <el-button :loading="confirmLoading" type="primary" @click="confirmFun">确 定</el-button>
    </span>
  </el-dialog>
</template>
<script>
export default {
  props:{
    confirmForm:{
      type: Object,
    },
    confirmLoading:{
      type:Boolean,
      default:false
    },
    confirmShow:{
      type:Boolean,
      default:false
    }
  },
  methods:{
    confirmFun(){
      if(this.confirmForm.amount == undefined || this.confirmForm.amount == null){
        this.$message.error('请输入退款金额');
        return;
      }
      // 判断运费
      if(this.confirmForm.isFreight == 0){
        if(this.confirmForm.amount > this.confirmForm.payPrice){
          this.$message.error('退款金额不可大于应退金额');
          return;
        }
      }
      if(this.confirmForm.isFreight == 1){
        if(this.confirmForm.amount > this.confirmForm.totalPayPrice){
          this.$message.error('退款金额不可大于应退金额');
          return;
        }
      }
      this.$emit('confirmFun', this.confirmForm);
    },
    confirmShowClose(){
      this.$emit('confirmShowClose',true)
    }
  }
}
</script>